@font-face {
  font-family: "Ubuntu-light";
  font-display: swap;
  font-weight: 300;
  src: url("../fonts/ubuntu-300.woff2") format("woff2"), url("../fonts/ubuntu-300.woff") format("woff");
}
@font-face {
  font-family: "Ubuntu-regular";
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/ubuntu-400.woff2") format("woff2"), url("../fonts/ubuntu-400.woff") format("woff");
}
@font-face {
  font-family: "Ubuntu-medium";
  font-display: swap;
  font-weight: 500;
  src: url("../fonts/ubuntu-500.woff2") format("woff2"), url("../fonts/ubuntu-500.woff") format("woff");
}
@font-face {
  font-family: "Ubuntu-bold";
  font-display: swap;
  font-weight: 700;
  src: url("../fonts/ubuntu-700.woff2") format("woff2"), url("../fonts/ubuntu-700.woff") format("woff");
}
html {
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
}

p {
  margin: 0 0 20px;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.3px;
  font-family: "Ubuntu-regular", Arial, Helvetica, sans-serif;
  color: #000000;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Добавил фон и высоту для body, чтобы показать фиксированный header при скролле*/
  background: #DAE5FF;
  height: 10000px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

input[type=text]::-ms-clear,
input[type=email]::-ms-clear,
input[type=tel]::-ms-clear,
input[type=password]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

input[type=text]::-ms-reveal,
input[type=email]::-ms-reveal,
input[type=tel]::-ms-reveal,
input[type=password]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

.header {
  padding-top: 20px;
  background: white;
}

.header.is-fixed {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  padding-bottom: 25px;
  animation-name: showHeader;
  animation-duration: 0.5s;
}

@-webkit-keyframes showHeader {
  0% {
    opacity: 0;
    top: -155px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}
@keyframes showHeader {
  0% {
    opacity: 0;
    top: -235px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}
.header.is-fixed .nav__list {
  display: none;
}

.header__wrap {
  display: flex;
  align-items: flex-end;
  margin-bottom: 34px;
}

.header.is-fixed .header__wrap {
  margin-bottom: 0;
}

.header__logo-link {
  margin-right: 15px;
}

.header__logo-img {
  display: block;
  width: 163px;
  height: 63px;
}

.header__address {
  position: relative;
  text-decoration: none;
  color: #001B5B;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  padding-left: 30px;
  max-width: 280px;
  margin-left: 44px;
}
.header__address:hover {
  color: #6695FF;
}
.header__address::before {
  position: absolute;
  display: block;
  content: "";
  width: 20px;
  height: 25px;
  bottom: 3px;
  left: 0;
  background: url(../images/location.svg) no-repeat center;
}
.header__address:hover::before {
  opacity: 0.7;
}

.header__mail {
  position: relative;
  text-decoration: none;
  color: #001B5B;
  font-size: 16px;
  line-height: 19px;
  font-family: "Ubuntu-medium", Arial, Helvetica, sans-serif;
  padding-left: 35px;
  margin-left: 44px;
  flex-shrink: 0;
}
.header__mail:hover {
  color: #6695FF;
}
.header__mail::before {
  position: absolute;
  display: block;
  content: "";
  width: 28px;
  height: 23px;
  bottom: 0;
  left: 0;
  background: url(../images/mail.svg) no-repeat center;
}
.header__mail:hover::before {
  opacity: 0.7;
}

.header__phone {
  margin-left: 44px;
  font-family: "Ubuntu-medium", Arial, Helvetica, sans-serif;
  flex-shrink: 0;
}

.header__search {
  position: relative;
  padding-left: 50px;
}
.header__search::after {
  position: absolute;
  display: block;
  content: "";
  top: 23px;
  left: 0;
  width: 29px;
  height: 29px;
}

.header__search-btn {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  box-shadow: none;
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  width: 40px;
  height: 40px;
  margin-right: 20px;
  margin-left: auto;
  background: url(../images/search.svg) no-repeat center;
}
.header__search-btn:hover {
  opacity: 0.7;
}

.header__request-btn {
  margin: 0;
  padding: 0 20px;
  padding-top: 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  box-shadow: none;
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  font-size: 14px;
  line-height: 20px;
  font-weight: 700;
  color: #FFFFFF;
  text-transform: uppercase;
  background-color: #e43939;
  border-radius: 10px;
  height: 57px;
  white-space: nowrap;
  transition: background-color 0.2s ease;
}
.header__request-btn:hover {
  background-color: #9C0000;
}

.container {
  width: 100%;
  max-width: 1410px;
  padding: 0 30px;
  margin: 0 auto;
}

.social {
  display: flex;
}

.social__link {
  margin-right: 5px;
}
.social__link:hover {
  opacity: 0.7;
}
.social__link:last-child {
  margin-right: 0;
}

.phone {
  display: flex;
  flex-direction: column;
  padding-left: 30px;
}

.phone__schedule {
  color: #001B5B;
  font-size: 11px;
  line-height: 13px;
  font-weight: 300;
}

.phone__link {
  text-decoration: none;
  color: #001B5B;
  text-transform: uppercase;
  position: relative;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  margin-right: 24px;
}
.phone__link::before {
  position: absolute;
  display: block;
  content: "";
  width: 21px;
  height: 22px;
  bottom: 3px;
  left: -28px;
  background: url(../images/phone.svg) no-repeat center;
}
.phone__link:hover {
  color: #6695FF;
}
.phone__link:hover::before {
  opacity: 0.7;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-around;
  font-size: 14px;
  line-height: 16px;
  color: #001B5B;
  text-transform: uppercase;
}

.nav__item {
  position: relative;
  padding: 10px 30px;
}
.nav__item:hover .nav__inner-list {
  display: block;
}

.nav__title {
  display: block;
  text-align: center;
  cursor: default;
  color: #001B5B;
  border-bottom: 2px solid transparent;
}

a.nav__title {
  cursor: pointer;
  text-decoration: none;
}
a.nav__title:hover {
  border-color: #0752FF;
}

.nav__inner-list {
  position: absolute;
  background-color: #FFFFFF;
  width: 240px;
  top: 36px;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  padding: 20px 20px;
  list-style: none;
  box-shadow: 3px 0 3px -3px rgba(0, 0, 0, 0.2), -3px 0 3px -3px rgba(0, 0, 0, 0.2);
  border-top: 2px solid #0752FF;
  border-bottom: 2px solid #0752FF;
  display: none;
}
.nav__inner-list::before {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-bottom: -1px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 5px 5px 5px;
  border-color: transparent transparent #0752FF transparent;
}

.nav__inner-item {
  margin-bottom: 20px;
  text-align: center;
}
.nav__inner-item:last-child {
  margin-bottom: 0;
}

.nav__link {
  text-decoration: none;
  color: #2B53AD;
}
.nav__link:hover {
  color: #6695FF;
}

.main {
  flex-grow: 1;
}

footer {
  padding: 20px;
}