.phone
    display: flex
    flex-direction: column
    padding-left: 30px

.phone__schedule
    color: $color-text-blue
    font-size: 11px
    line-height: 13px
    font-weight: 300

.phone__link
    text-decoration: none
    color: $color-text-blue
    text-transform: uppercase
    position: relative
    font-size: 16px
    line-height: 19px
    font-weight: 500
    margin-right: 24px
    &::before
        position: absolute
        display: block
        content: ""
        width: 21px
        height: 22px
        bottom: 3px
        left: -28px
        background: url(../images/phone.svg) no-repeat center
    &:hover
        color: $color-link-hover
        &::before
            opacity: 0.7
