.nav__list
    margin: 0
    padding: 0
    list-style: none
    display: flex
    justify-content: space-around
    font-size: 14px
    line-height: 16px
    color: $color-text-blue
    text-transform: uppercase

.nav__item
    position: relative
    padding: 10px 30px
    &:hover .nav__inner-list
        display: block

.nav__title
    display: block
    text-align: center
    cursor: default
    color: $color-text-blue
    border-bottom: 2px solid transparent

a.nav__title
    cursor: pointer
    text-decoration: none
    &:hover
        border-color: $color-list-border

.nav__inner-list
    position: absolute
    background-color: $color-white
    width: 240px
    top: 36px
    left: 50%
    transform: translateX(-50%)
    margin: 0
    padding: 20px 20px  
    list-style: none
    box-shadow: 3px 0 3px -3px rgba(0,0,0,0.2), -3px 0 3px -3px rgba(0,0,0,0.2)
    border-top: 2px solid $color-list-border
    border-bottom: 2px solid $color-list-border
    display: none
    &::before
        content: ""
        position: absolute
        bottom: 100%
        left: 50%
        transform: translate(-50%, -50%)
        margin-bottom: -1px
        width: 0
        height: 0
        border-style: solid
        border-width: 0 5px 5px 5px
        border-color: transparent transparent $color-list-border transparent

.nav__inner-item
    margin-bottom: 20px
    text-align: center
    &:last-child
        margin-bottom: 0

.nav__link
    text-decoration: none
    color: $color-text-lightblue
    &:hover
        color: $color-link-hover


