.header
    padding-top: 20px
    background: white

.header.is-fixed
    position: fixed
    width: 100%
    top: 0
    left: 0
    padding-bottom: 25px
    animation-name: showHeader
    animation-duration: .5s

@-webkit-keyframes showHeader
  0%
    opacity: 0
    top: -155px
  100%
    opacity: 1
    top: 0

@keyframes showHeader
  0%
    opacity: 0
    top: -235px
  100%
    opacity: 1
    top: 0

.header.is-fixed .nav__list
    display: none

.header__wrap
    display: flex
    align-items: flex-end
    margin-bottom: 34px

.header.is-fixed .header__wrap
    margin-bottom: 0

.header__logo-link
    margin-right: 15px

.header__logo-img
    display: block
    width: 163px
    height: 63px

.header__address
    position: relative
    text-decoration: none
    color: $color-text-blue
    font-size: 16px
    line-height: 18px
    font-weight: 400
    padding-left: 30px
    max-width: 280px
    margin-left: 44px
    &:hover
        color: $color-link-hover
    &::before
        position: absolute
        display: block
        content: ""
        width: 20px
        height: 25px
        bottom: 3px
        left: 0
        background: url(../images/location.svg) no-repeat center
    &:hover
        &::before
            opacity: 0.7

.header__mail
    position: relative
    text-decoration: none
    color: $color-text-blue
    font-size: 16px
    line-height: 19px
    font-family: $font-500
    padding-left: 35px
    margin-left: 44px
    flex-shrink: 0
    &:hover
        color: $color-link-hover
    &::before
        position: absolute
        display: block
        content: ""
        width: 28px
        height: 23px
        bottom: 0
        left: 0
        background: url(../images/mail.svg) no-repeat center
    &:hover
        &::before
            opacity: 0.7

.header__phone
    margin-left: 44px
    font-family: $font-500
    flex-shrink: 0
    
.header__search
    position: relative
    padding-left: 50px
    &::after
        position: absolute
        display: block
        content: ""
        top: 23px
        left: 0
        width: 29px
        height: 29px

.header__search-btn
    margin: 0
    padding: 0
    display: flex
    justify-content: center
    align-items: center
    border: 0
    box-shadow: none
    -webkit-appearance: none
    appearance: none
    cursor: pointer
    width: 40px
    height: 40px
    margin-right: 20px
    margin-left: auto
    background: url(../images/search.svg) no-repeat center
    &:hover
        opacity: 0.7
        
.header__request-btn
    margin: 0
    padding: 0 20px
    padding-top: 1px
    display: flex
    justify-content: center
    align-items: center
    border: 0
    box-shadow: none
    -webkit-appearance: none
    appearance: none
    cursor: pointer
    font-size: 14px
    line-height: 20px
    font-weight: 700
    color: $color-white
    text-transform: uppercase
    background-color: $color-btn-bg
    border-radius: 10px
    height: 57px
    white-space: nowrap
    transition: background-color 0.2s ease
    &:hover
        background-color: $color-btn-hover



